.page-header {
  background: $blue-background-gradient;
  padding: 25px 0 30px 0;
  width: 100%;
  position: fixed;
  z-index: 3;

  @media (max-width: 1023px) {
    position: static;
    padding: 20px 20px 25px 0;
  }

  @media (max-width: 767px) {
    position: static;
    padding: 15px 11px 20px 0;
  }
}
